import { useCallback, useEffect, useState } from 'react';
import { useSelector } from 'react-redux';
import { cloneDeep } from 'lodash';
import isEqual from 'lodash/isEqual';
import { EstimateMeisaiModel, MeisaiXML } from '../../../../../../model/estimate/estimate-meisai.model';
import { State } from '../../../../../../redux/root.reducer';
import { XmlParser } from '../../../../../../parser/xml-parser';
import { dummy } from '../../../../../../model/estimate/dummy';
import { Resize } from '../../../../../ui/resize/resize';
import { EstimateTree } from './estimate-tree';
import { EditPriceAreaState } from '../../../../../../type/estimate/estimate.type';
import { EstimateMeisaiListPC } from './estimate-meisai-list.pc';

export type SelectTreeData = {
  daibunrui_id?: string;
  tyubunrui_id?: string;
}

type Props = {
  // modelData?: MeisaiXML;
  priceData: EditPriceAreaState;
  allowEdit?: boolean;
  callback: (meisaiList:EstimateMeisaiModel) => void;
}

export const ExplorePC = (props: Props) => {
  const {
    priceData,
    allowEdit = true,
    callback,
  } = props;

  const estimate = useSelector((state: State) => state.estimate.estimate, isEqual);

  const [meisaiModel, setMeisaiModel] = useState<EstimateMeisaiModel | null>(null);
  const [selectTree, setSelectTree] = useState<SelectTreeData | null>(null);

  const changeSelectTree = useCallback((v: SelectTreeData) => {
    setSelectTree(cloneDeep(v));
    // if (callbackSelectCategory) { callbackSelectCategory(v); }
  }, []);

  // Parse XML → Object
  useEffect(() => {
    new XmlParser().parse<MeisaiXML>(dummy).then((res) => {
      setMeisaiModel(new EstimateMeisaiModel(cloneDeep(res)));
    });
  }, [estimate?.id]);

  useEffect(() => {
    if (meisaiModel)callback(meisaiModel);
  }, [meisaiModel]);

  return (
    <>
      <div className="price_box">
        <div>見積金額合計 : <strong>¥{priceData.estimate_total_price.toLocaleString() ?? 0}</strong></div>
        <div>原価金額合計 : <strong>¥{priceData.genka_total_price.toLocaleString() ?? 0}</strong></div>
        <div>粗利額 : <strong>¥{priceData.arari_price.toLocaleString() ?? 0}</strong></div>
        <div>粗利率 : <strong> {priceData.arari_percent.toLocaleString() ?? 0}%</strong></div>
      </div>
      <div className="estimate_box">
        <Resize enabled={{ right: true }}>
          <EstimateTree
            meisaiModel={meisaiModel}
            allowEdit={allowEdit}
            callback={changeSelectTree}
            callbackModel={setMeisaiModel}
          />
        </Resize>
        <EstimateMeisaiListPC
          model={meisaiModel}
          treeData={selectTree}
          callbackModel={() => {}}
          callback={() => {}}
          allowEdit={allowEdit}
        />
      </div>
    </>
  );
};
