import {
  useCallback, useEffect, useMemo, useState,
} from 'react';
import { useDispatch } from 'react-redux';
import { goBack } from 'connected-react-router';
import { cloneDeep } from 'lodash';
import { DisplayElements } from '../../../../../../type/display-elements.type';
import { Table, TableOption } from '../../../../../ui/table/table';
import { RightLabelCheckbox } from '../../../../../ui/checkbox/right-label-checkbox/right-label-checkbox';
import { LeftIconButton } from '../../../../../ui/button/left-icon-button/left-icon-button';
import { Button } from '../../../../../ui/button/button';

export type MasterGetListParam = {
  order: number;
  sort: number;
  isMuko: boolean;
}

type Props = {
  header: { key: string; label: string;}[];
  rowDataList: any[];
  list: DisplayElements[][];
  callbackEdit: (v?: any) => void;
  callbackGetList: (v: MasterGetListParam) => void;
  defaultOrder: number;
  tableOption?: TableOption;
};

export const MasterBody = (props: Props) => {
  const {
    rowDataList,
    header,
    callbackGetList,
    callbackEdit,
    list,
    tableOption,
    defaultOrder,
  } = props;

  const dispatch = useDispatch();

  /* State */
  const [isSort, setIsSort] = useState(false);
  const [showList, setShowList] = useState<DisplayElements[][]>([]);
  const [rowList, setRowList] = useState<any[]>([]);
  const [selected, setSelected] = useState(NaN);
  const [isMuko, setIsMuko] = useState(false);
  const [orderSort, setOrderSort] = useState<{
    order: number;
    sort: number;
  } >({
    order: defaultOrder,
    sort: 1,
  });

  /* Callback */
  const handleClickRow = useCallback(
    (row: any) => {
      setSelected(cloneDeep(rowDataList.findIndex((v2) => v2.id === row.id)));
    }, [rowDataList],
  );
  const handleDbClickRow = useCallback(
    (row: any) => callbackEdit(row.id),
    [callbackEdit],
  );

  const getList = useCallback(() => {
    callbackGetList({
      ...orderSort,
      isMuko,
    });
  },
  [callbackGetList, orderSort, isMuko]);

  const sortStart = useCallback(() => {
    setSelected(NaN);
    setIsSort(true);
  }, []);

  const sortEnd = useCallback(() => {
    setSelected(NaN);
    setIsSort(false);
  }, []);

  const sortCancel = useCallback(() => {
    sortEnd();
    setShowList(cloneDeep(list));
    setRowList(cloneDeep(rowDataList));
  }, [list, rowDataList]);

  const onMove = useCallback((_showList: DisplayElements[][], _rowList: any[]) => {
    _showList.forEach((v) => {
      v.shift();
    });
    setShowList(cloneDeep(_showList));
    setRowList(cloneDeep(_rowList));
  }, []);

  /* Effect */
  useEffect(() => {
    getList();
  }, [orderSort]);

  useEffect(() => {
    setShowList(cloneDeep(list));
  }, [list]);

  useEffect(() => {
    setRowList(cloneDeep(rowDataList));
  }, [rowDataList]);

  const sendList = useMemo(() => showList.map((v: any, i) => ([
    !isSort ? (
      <Button
        color="secondary"
        size="md"
        onClick={(e) => {
          e.stopPropagation();
          callbackEdit(rowDataList[i].id);
        }}
      >
        編集
      </Button>
    ) : <i className="fas fa-bars" />,
    ...v,
  ])), [showList, rowDataList, isSort]);

  return (
    <div className="main_cnt">
      <div className="search_area only_simple ">
        <div className="item_wrap">
          <div className="item_box">
            <RightLabelCheckbox
              checked={isMuko}
              label="無効情報も含む"
              onChange={() => setIsMuko(!isMuko)}
            />
          </div>
          <LeftIconButton
            label="絞込み"
            fontAwesomeClass="fas fa-filter"
            className="btn_search for_simple"
            size="sm"
            color="secondary"
            onClick={getList}
          />
        </div>
      </div>
      <div className="option_area table_sort">
        <div className="left">
          <div className="count">総件数： &nbsp;<span>{list.length}</span> 件</div>
        </div>
        <div className="right" style={{ display: 'flex' }}>
          {isSort
            ? (
              <>
                <Button
                  color="dark"
                  size="md"
                  onClick={sortCancel}
                >
                  キャンセル
                </Button>
                <Button
                  color="secondary"
                  size="md"
                  onClick={sortEnd}
                >
                  登録
                </Button>
              </>
            )
            : (
              <>
                <Button
                  color="primary"
                  size="md"
                  onClick={sortStart}
                >
                  並び替え
                </Button>
                <LeftIconButton
                  label="新規登録"
                  size="md"
                  fontAwesomeClass="fas fa-edit"
                  className="btn_search for_detail"
                  color="primary"
                  onClick={callbackEdit}
                />
              </>
            )}
        </div>
      </div>
      <section className="result_area list_area">
        <div className="inner">
          <div className="table_responsive">
            {/* <Table /> */}
            <Table
              className="table_selectable table_sortable table_sticky"
              header={header.map((v) => v.label)}
              onClickRow={handleClickRow}
              onDbClick={handleDbClickRow}
              selectedTr={[selected]}
              rowDataList={rowDataList}
              // ids={cloneList.map((v)=>(v.idx))}
              sort={{ onClick: (order, sort) => setOrderSort({ order, sort }) }}
              lists={sendList}
              callbackMoveList={onMove}
              option={tableOption}
              isDnD={isSort}
            />
          </div>
        </div>
      </section>
      <footer className="btn_area">
        <div className="left_box" />
        <div className="right_box">
          <LeftIconButton
            label="戻る"
            fontAwesomeClass="fas fa-arrow-left"
            size="md"
            color="dark"
            onClick={() => dispatch(goBack())}
          />
        </div>
      </footer>
    </div>
  );
};
