import { cloneDeep } from 'lodash';
import { XmlParser } from '../../parser/xml-parser';
import { MathHelper as MH } from '../../utilities/math-helper';
import { deleteComma } from '../../utilities/delete-comma';
import { dummy } from './dummy';
import { EstimateMeisaiCollection } from './estimate-meisai.collection';

export type AddData = Pick<MeisaiListXML['$'],
  'daibunrui_id' | 'daibunrui_name' | 'print_name' | 'suryou' |
  'id' | 'kingaku' | 'tani_id' | 'shohin_kubun' | 'shohin_kubun_name' |
  'tyubunrui_name' | 'tyubunrui_id' | 'shohin_cd' | 'name' | 'tani_name'
>

export type MeisaiListXML = {
  $: {
    arari_ritu: string;
    daibunrui_id: string;
    daibunrui_name: string;
    daibunrui_name_out: string;
    filename: string;
    genka: string;
    genka_kei: number;
    id: number;
    identity_id: number;
    kikaku: string;
    kingaku: number;
    name: string;
    print_name: string;
    row: number;
    server_filename: string;
    shiiresaki_id: string;
    shiiresaki_name: string;
    shikiri_kakaku: string;
    shohin_cd: string;
    shohin_kubun: string;
    shohin_kubun_name: string;
    shohizeigaku: string;
    suryou: string;
    tani_id: string;
    tani_name: string;
    tyubunrui_id: string;
    tyubunrui_name: string;
    tyubunrui_name_out: string;
    valid_flag: boolean;
  };
}

export type MeisaiXML = {
  meisai: {
    meisai: {
      mei: MeisaiListXML[];
    }[];
  };
};

export class EstimateMeisaiModel {
  public list: MeisaiListXML[] = [];

  private defaultData: MeisaiXML;

  constructor(data: MeisaiXML) {
    this.defaultData = cloneDeep(data);
    this.list = cloneDeep(data.meisai.meisai[0].mei);
    this.format();
  }

  format() {
    this.list.forEach((val, i) => {
      const v = val.$;
      const row = this.list[i].$;

      row.id = i + 1;
      row.row = i + 1;
      row.identity_id = i + 1;
      row.kingaku = MH.rounding(deleteComma(v.kingaku), 0);
      row.genka_kei = MH.rounding(deleteComma(v.genka_kei), 0);

      const tempMitsu = Number(v.kingaku); // 見積計
      const tempGenka = Number(v.genka_kei); // 原価計
      const tempArari = Number(v.kingaku) - Number(v.genka);

      if (!tempArari || Number.isNaN(MH.rounding((1 - (tempGenka / tempMitsu)) * 100))) {
        row.arari_ritu = '0';
      } else if (!Number.isFinite(
        Math.round((1 - (tempGenka / tempMitsu)) * 100),
      )) { // 値が有限大で無い場合 見積金が０の場合
        row.arari_ritu = '-';
      } else if (
        !Number.isNaN(Math.round((1 - (tempGenka / tempMitsu)) * 100))
        && Number.isFinite(Math.round((1 - (tempGenka / tempMitsu)) * 100))
        && (tempArari < 0)) {
        row.arari_ritu = String(Math.abs(Math.round((1 - (tempGenka / tempMitsu)) * 100)) * (-1));
      } else if (!Number.isNaN(Math.round((1 - (tempGenka / tempMitsu)) * 100))
        && Number.isFinite(Math.round((1 - (tempGenka / tempMitsu)) * 100))
        && (tempArari > 0)) {
        row.arari_ritu = String(Math.abs(Math.round((1 - (tempGenka / tempMitsu)) * 100)));
      }
      // wkKingaku += Number( this._nh.cancelFigureSeparate(meisaiList[i].@kingaku));
    });

    const searchArr:ReadonlyArray<keyof MeisaiListXML['$']> = [
      'daibunrui_name',
      'tyubunrui_name',
      'shohin_kubun_name',
      'name',
      'tani_name',
      'kikaku',
      'suryou',
      'genka',
      'shikiri_kakaku',
      'daibunrui_id',
      'tyubunrui_id',
      'shohin_kubun',
      'shohin_cd',
      'tani_id',
      'valid_flag',
      'filename',
      'server_filename',
      'genka_kei',
      'daibunrui_name_out',
      'tyubunrui_name_out',
      'print_name',
    ];

    for (let j = 0; j < this.list.length; j += 1) {
      let currentCount = 1;
      for (let k = 0; k < this.list.length; k += 1) {
        // インデックスが同じ場合はチェック対象外
        if (j !== k) {
          let currentFlag = true;

          // ID毎に中身チェック
          for (let l = 0; l < searchArr.length; l += 1) {
            if (this.list[j].$[searchArr[l]].toString()
              !== this.list[k].$[searchArr[l]].toString()) {
              currentFlag = false;
              break;
            }
          }
          // jの明細とkの明細が同じだったら、IDふる。
          if (currentFlag) {
            currentCount += 1;
            this.list[j].$.identity_id = currentCount;
          }
        }
      }
    }
  }

  // eslint-disable-next-line
  add(data: MeisaiListXML['$']) {
    const temp = cloneDeep(data);
    const tempMitsu = Number(data.kingaku); // 見積計
    const tempGenka = Number(data.genka_kei); // 原価計
    const tempArari = Number(data.kingaku) - Number(data.genka);

    if (!tempArari || Number.isNaN(MH.rounding((1 - (tempGenka / tempMitsu)) * 100))) {
      temp.arari_ritu = '0';
    } else if (!Number.isFinite(
      Math.round((1 - (tempGenka / tempMitsu)) * 100),
    )) { // 値が有限大で無い場合 見積金が０の場合
      temp.arari_ritu = '-';
    } else if (
      !Number.isNaN(Math.round((1 - (tempGenka / tempMitsu)) * 100))
      && Number.isFinite(Math.round((1 - (tempGenka / tempMitsu)) * 100))
      && (tempArari < 0)) {
      temp.arari_ritu = String(Math.abs(Math.round((1 - (tempGenka / tempMitsu)) * 100)) * (-1));
    } else if (!Number.isNaN(Math.round((1 - (tempGenka / tempMitsu)) * 100))
      && Number.isFinite(Math.round((1 - (tempGenka / tempMitsu)) * 100))
      && (tempArari > 0)) {
      temp.arari_ritu = String(Math.abs(Math.round((1 - (tempGenka / tempMitsu)) * 100)));
    }
    this.list.push({ $: temp });
    // eslint-disable-next-line
    const addData: Partial<MeisaiListXML> = {
      $: {
        ...temp,
        shiiresaki_name: '',
        shiiresaki_id: '',
        valid_flag: true,
        kingaku: MH.rounding(Number(data.suryou) * Number(data.shikiri_kakaku), 0),
        genka_kei: MH.rounding(Number(data.suryou) * Number(data.genka), 0),
        daibunrui_name_out: data.daibunrui_name,
        tyubunrui_name_out: data.tyubunrui_name,
        print_name: data.name,
      },
    };
    // this.list.forEach((v) => {
    // })
  }

  remove(ids: (string | number)[]) {
    this.list = this.list.filter((v) => {
      let flag = true;
      for (let i = 0; i < ids.length; i += 1) {
        if (v.$.id === ids[i]) {
          flag = false;
          break;
        }
      }
      return flag;
    });
  }

  // eslint-disable-next-line
  changeName(
    mode: 'tree-daibunrui' | 'tree-tyubunrui' | 'list',
    data: MeisaiListXML['$'],
  ) {
    if (mode === 'tree-daibunrui' || mode === 'tree-tyubunrui') {
      const list = this.list.filter((v) => {
        if (mode === 'tree-daibunrui') {
          return v.$.daibunrui_id === data.daibunrui_id;
        }
        return (v.$.daibunrui_id === data.daibunrui_id)
          && (v.$.tyubunrui_id === data.tyubunrui_id);
      });
      list.forEach((v) => {
        this.edit({ ...v.$, print_name: data.print_name });
      });
      return;
    }
    this.edit(data);
  }

  // eslint-disable-next-line
  moveTree(mode:'up' | 'down', daibunrui_id: string, tyubunrui_id?: string) {
    let list:MeisaiListXML[] = [];
    // eslint-disable-next-line
    if (tyubunrui_id) {
      list = this.list.filter((v) => (
        v.$.tyubunrui_id === tyubunrui_id
        && v.$.daibunrui_id === daibunrui_id
      ));
    } else {
      list = this.list.filter((v) => (
        v.$.daibunrui_id === daibunrui_id
      ));
    }
    this.move(mode, list);
  }

  // eslint-disable-next-line
  move(mode: 'up' | 'down', selectData:MeisaiListXML[]) {
    const cloneList = cloneDeep(this.list);
    const selectList: number[] = [];
    selectData.forEach((v) => {
      selectList.push(cloneList.findIndex((data) => data.$.id === v.$.id));
    });
    if (mode === 'up') {
      /** index を昇順で並び替え */
      selectList.sort((a, b) => (a - b));
      selectList.forEach((v, i) => {
        /** 選択中のレコードで移動不可かつ一つ上のもの、一つ上のレコードの中分類が異なるものがないかの精査 */
        if (
          !selectList.find((data) => (v - 1 === data))
          && cloneList[v]?.$.tyubunrui_id === cloneList[v - 1]?.$.tyubunrui_id
        ) {
          /** リストの入れ替えと index の更新 */
          const temp = cloneDeep(cloneList[v - 1]);
          cloneList[v - 1] = cloneDeep(cloneList[v]);
          cloneList[v] = temp;
          selectList[i] = v - 1;
        }
      });
    } else {
      selectList.sort((a, b) => (b - a));
      selectList.forEach((v, i) => {
        /** 選択中のレコードで移動不可かつ一つ下のもの、一つ下のレコードの中分類が異なるものがないかの精査 */
        if (
          !selectList.find((data) => (v + 1 === data))
          && cloneList[v]?.$.tyubunrui_id === cloneList[v + 1]?.$.tyubunrui_id
        ) {
          /** リストの入れ替えと index の更新 */
          const temp = cloneDeep(cloneList[v + 1]);
          cloneList[v + 1] = cloneDeep(cloneList[v]);
          cloneList[v] = temp;
          selectList[i] = v + 1;
        }
      });
    }
    this.list = cloneDeep(cloneList);
    return selectList;
  }

  // eslint-disable-next-line
  edit(data: MeisaiListXML['$']) {
    const index = this.list.findIndex((v) => v.$.id === data.id);
    this.list[index].$ = cloneDeep(data);
  }

  obj2Xml() {
    return new XmlParser().build<MeisaiXML>({
      ...this.defaultData,
      meisai: {
        meisai: [
          { mei: this.list },
        ],
      },
    });
  }
}

