new XmlParser().parse<MeisaiXML>(dummy).then((res) => {
  const modelTest = new EstimateMeisaiModel(res);
  // console.log('modelTest', modelTest.list);
  modelTest.add({
    ...EstimateMeisaiCollection.initialAdd(),
    id: 0 || 0,
    daibunrui_id: '',
    daibunrui_name: '',
    tyubunrui_id: '',
    tyubunrui_name: '',
    print_name: '' || '',
  });
  modelTest.edit({
    ...EstimateMeisaiCollection.initialAdd(),
    id: 0 || 0,
    daibunrui_id: '',
    daibunrui_name: '111111111111111111',
    tyubunrui_id: '',
    tyubunrui_name: '',
    print_name: '' || '',
  });
  // console.log('list :', modelTest.list);
});

