import React, {
  useCallback, useEffect, useMemo, useState,
} from 'react';
import { useDispatch } from 'react-redux';
import * as lodash from 'lodash';
import { cloneDeep } from 'lodash';
import { Table } from '../../../../../ui/table/table';
import './estimate-meisai-list.pc.scss';
import { EstimateCollection } from '../../../../../../collection/estimate/estimatecollection';
import { Button } from '../../../../../ui/button/button';
import { DialogActions } from '../../../../../../redux/dialog/dialog.action';
import { ChangePrintNameDialogPC } from '../../edit/change-print-name-dialog/change-print-name-dialog.pc';
import { AddMasterMeisai } from '../../edit/add-master-meisai/add-master-meisai';
import { MathHelper } from '../../../../../../utilities/math-helper';
import { Input } from '../../../../../ui/input/input';
import { MeisaiListXML, EstimateMeisaiModel } from '../../../../../../model/estimate/estimate-meisai.model';
import { SelectTreeData } from './estimate-explore.pc';

export type SelectData = { daibunrui: string; chubunrui: string; }

const tdAlign: {index: number, align: 'right'}[] = [
  { index: 5, align: 'right' },
  { index: 6, align: 'right' },
  { index: 7, align: 'right' },
  { index: 8, align: 'right' },
  { index: 9, align: 'right' },
];

type Props = {
  model: EstimateMeisaiModel | null;
  treeData: Partial<SelectTreeData> | null,
  callback: (v: MeisaiListXML) => void;
  callbackModel: (v: EstimateMeisaiModel) => void;
  masterAdd?: boolean;
  allowEdit?: boolean;
}

type InputTableElementProps = {
  value: string,
  callbackBlur: (v: string | number) => void,
  type?: string,
  alignRight?: boolean;
};

const InputTableElement = (props: InputTableElementProps) => {
  // const dispatch = useDispatch();
  const {
    value: _value,
    type,
    callbackBlur,
    alignRight,
  } = props;
  const [value, setValue] = useState(_value);
  const handlerChangeValue = useCallback((v) => {
    setValue(v);
  }, []);
  const handlerBlur = useCallback(() => {
    if (value !== _value) {
      callbackBlur(value);
      if (type === 'number' && !value) {
        setValue('0');
      }
    }
  }, [_value, value]);
  useEffect(() => {
    setValue(_value);
  }, [_value]);
  return (
    <Input
      value={value}
      alignRight={alignRight}
      onChange={(e) => handlerChangeValue(e.target.value)}
      onBlur={handlerBlur}
      type={type}
    />
  );
};

export const EstimateMeisaiListPC = (props: Props) => {
  const {
    // eslint-disable-next-line
    model, callback, masterAdd, callbackModel, treeData, allowEdit,
  } = props;

  /* Hooks */
  const dispatch = useDispatch();

  /* State */
  const [selected, setSelected] = useState<number[]>(cloneDeep([]));
  const [meisaiList, setMeisaiList] = useState<MeisaiListXML[]>([]);
  const [selectData, setSelectData] = useState<MeisaiListXML[]>([]);

  const moveDisabled = useMemo(() => !selectData.length || [...selectData].filter(
    (v) => v.$.tyubunrui_id === selectData[0].$.tyubunrui_id,
  ).length !== selectData.length, [selectData]);

  /* Callback */
  const handleClickRow = useCallback((v: MeisaiListXML) => {
    if (!Object.keys(v).length) {
      setSelectData([]);
      setSelected([]);
      return;
    }
    setSelectData([cloneDeep(v)]);
    callback(cloneDeep(v));
    const findIndex = meisaiList.findIndex((v2) => v2.$.id === v.$.id);
    if (findIndex || findIndex === 0 || findIndex !== -1) {
      setSelected([findIndex]);
      return;
    }
    setSelectData([]);
    setSelected([]);
  },
  [callback, meisaiList, model?.list]);

  const handleClickMove = useCallback(
    (type:'up' | 'down') => {
      const cloneList = lodash.cloneDeep(meisaiList);
      const selectList: number[] = [];
      selectData.forEach((v) => {
        selectList.push(cloneList.findIndex((data) => data.$.tyubunrui_id === v.$.tyubunrui_id));
      });
      if (type === 'up') {
        /** index を昇順で並び替え */
        selectList.sort((a, b) => (a - b));
        selectList.forEach((v, i) => {
          /** 選択中のレコードで移動不可かつ一つ上のもの、一つ上のレコードの中分類が異なるものがないかの精査 */
          if (
            !selectList.find((data) => (v - 1 === data))
            && cloneList[v]?.$.tyubunrui_id === cloneList[v - 1]?.$.tyubunrui_id
          ) {
            /** リストの入れ替えと index の更新 */
            const temp = lodash.cloneDeep(cloneList[v - 1]);
            cloneList[v - 1] = lodash.cloneDeep(cloneList[v]);
            cloneList[v] = temp;
            selectList[i] = v - 1;
          }
        });
      } else {
        selectList.sort((a, b) => (b - a));
        selectList.forEach((v, i) => {
          /** 選択中のレコードで移動不可かつ一つ下のもの、一つ下のレコードの中分類が異なるものがないかの精査 */
          if (
            !selectList.find((data) => (v + 1 === data))
            && cloneList[v]?.$.tyubunrui_id === cloneList[v + 1]?.$.tyubunrui_id
          ) {
            /** リストの入れ替えと index の更新 */
            const temp = lodash.cloneDeep(cloneList[v + 1]);
            cloneList[v + 1] = lodash.cloneDeep(cloneList[v]);
            cloneList[v] = temp;
            selectList[i] = v + 1;
          }
        });
      }
      setMeisaiList(lodash.cloneDeep(cloneList));
      setSelected(cloneDeep(selectList));
      // if (model) { callbackModel({ ...model, list: cloneList }); }
    },
    [selectData],
  );

  const handleClickChangePrintName = useCallback(
    () => {
      if (selectData.length !== 1) return;
      dispatch(DialogActions.push({
        title: '見積印刷名称設定',
        element: <ChangePrintNameDialogPC
          name={selectData[0].$.name}
          printName={selectData[0].$.print_name}
          callback={() => {
            dispatch(DialogActions.pop());
          }}
        />,
      }));
    },
    [selectData, meisaiList],
  );

  /* 見積明細 新規作成・編集 */
  const handleClickMeisaiEdit = useCallback(
    (mode: 'add' | 'edit', rowData?:MeisaiListXML) => {
      let data: MeisaiListXML | undefined;

      if (mode === 'edit') {
        if (rowData || selectData.length === 1) {
          // eslint-disable-next-line
          data = lodash.cloneDeep(rowData || selectData[0]);
        }
      }

      // dispatch(DialogActions.push({
      //   title: '見積明細作成',
      //   className: '',
      //   element: <EditMeisaiDialogPC
      //     estimateId={estimateId}
      //     data={data}
      //     callback={() => {
      //       getList();
      //       // dispatch(EstimateActions.api.meisai.getList({
      //       //   param: {
      //       //     id: estimateId,
      //       //     data: {
      //       //       category: selectBunrui?.daibunrui,
      //       //       sub_category: selectBunrui?.chubunrui,
      //       //     },
      //       //   },
      //       //   callback: (v) => callbackSetList(cloneDeep(v)),
      //       // }));
      //     }}
      //   />,
      // }));
    },
    [selectData, meisaiList, treeData],
  );

  /* ダブルクリックハンドラ */
  const handleDbClickRow = useCallback(
    (v: MeisaiListXML) => {
      handleClickRow(cloneDeep(v));
      handleClickMeisaiEdit('edit', cloneDeep(v));
    },
    [handleClickRow, handleClickMeisaiEdit],
  );

  /* 明細削除 */
  const handleClickDelete = useCallback(
    () => {
      if (!selectData.length) return;

      dispatch(DialogActions.pushMessage({
        title: '見積明細削除',
        message: ['削除しますか'],
        isCancel: true,
        callback: () => {
        },
      }));
    },
    [selectData, treeData],
  );

  /* マスタから迷彩登録 */
  const handleClickAddMasterMeisai = useCallback(
    () => {
      dispatch(DialogActions.push({
        title: '見積明細登録',
        className: 'max_height_dialog max_width_dialog',
        element: <AddMasterMeisai
          callback={() => {}}
        />,
      }));
    },
    [],
  );

  const onClickMulti = useCallback((v: MeisaiListXML[]) => {
    setSelected(v.map((v2) => meisaiList.findIndex((v3) => v3.$.id === v2.$.id)));
    const sList:MeisaiListXML[] = [];
    v.forEach((v2) => {
      const findData = meisaiList.find((v3) => v2.$.id === v3.$.id);
      if (findData) {
        sList.push(findData);
      }
    });
    setSelectData(sList);
  }, [meisaiList]);

  const createMeisaiList = useCallback(() => {
    if (!model) return;
    const _meisaiList = model.list.filter((v) => {
      const { daibunrui_id, tyubunrui_id } = v.$;
      if (treeData?.tyubunrui_id) {
        return daibunrui_id === treeData.daibunrui_id && tyubunrui_id === treeData.tyubunrui_id;
      }
      if (treeData?.daibunrui_id) {
        return daibunrui_id === treeData.daibunrui_id;
      }
      return true;
    });
    setMeisaiList(cloneDeep(_meisaiList));
  }, [model?.list, treeData]);

  /* Effect */
  useEffect(() => {
    setSelectData([]);
    setSelected([]);
    createMeisaiList();
    return () => {
      setMeisaiList([]);
    };
  }, [model, treeData]);

  const borderSpan = useMemo(() => [...meisaiList]
    .map((v, i) => (v.$.tyubunrui_id !== meisaiList[i + 1]?.$.tyubunrui_id ? i : false))
    .filter((v) => !!v) as number[], [meisaiList]);

  return (
    <section className="result_area list_area">
      <div className="inner">
        <div className="table_responsive">
          <Table
            className="table_selectable table_sortable table_sticky"
            header={EstimateCollection.estimateMeisaiHeader}
            selectedTr={selected}
            rowDataList={meisaiList}
            onDbClick={handleDbClickRow}
            onClickMulti={onClickMulti}
            onClickRow={handleClickRow}
            lists={meisaiList.map((v) => (
              [
                v.$.name,
                v.$.print_name || v.$.name,
                <InputTableElement
                  value={v.$.kikaku}
                  callbackBlur={() => {}}
                />,
                <InputTableElement
                  value={v.$.suryou ? MathHelper.rounding2Str(Number(v.$.suryou), 2, 'round', true) : '0'}
                  callbackBlur={() => {}}
                  type="number"
                  alignRight
                />,
                v.$.tani_name,
                'unit_price',
                v.$.kingaku.toLocaleString(),
                'prime_cost',
                'cost_amount',
                'gross_profit_amount',
                'gross_profit_rate',
                // v.remarks,
                <InputTableElement
                  value="remarks"
                  callbackBlur={() => {}}
                />,
              ]
            ))}
            option={{ borderSpan, tdAlign }}
          />
        </div>
      </div>
      {(!masterAdd && allowEdit)
        && (
        <div className="btn_box">
          <Button size="sm" color="secondary" onClick={() => handleClickMove('up')} disabled={moveDisabled}>移動 ▲</Button>
          <Button size="sm" color="secondary" onClick={() => handleClickMove('down')} disabled={moveDisabled}>移動 ▼</Button>
          <Button size="sm" color="secondary" onClick={handleClickChangePrintName} disabled={selectData.length !== 1}>印刷名称変更</Button>
          <Button size="sm" color="secondary" onClick={() => handleClickMeisaiEdit('add')}>直接登録</Button>
          <Button size="sm" color="secondary" onClick={handleClickAddMasterMeisai}>マスタから登録</Button>
          <Button size="sm" color="secondary" onClick={() => handleClickMeisaiEdit('edit')} disabled={selectData.length !== 1}>編集</Button>
          <Button size="sm" color="dark" onClick={handleClickDelete} disabled={!selectData.length}>削除</Button>
        </div>
        )}
    </section>
  );
};

EstimateMeisaiListPC.defaultProps = {
  allowEdit: true,
};
