import { reducerWithInitialState } from 'typescript-fsa-reducers';
import { cloneDeep } from 'lodash';
import { User } from '../../type/auth/user.typs';
import { AuthActions } from './auth.action';

export type AuthState = {
  user: User
};

const initialState = {
  // user: null,
  user: {
    company_id: 0,
    employee_id: 0,
    id: 3,
    name: 'TESTTESTTEST',
    store_id: 0,
    employee_cd: 'admin-test',
    short_name: 'short-test',
    furigana: 'てすと',
    job_title: 'テストタイトルtest',
    sales_target: 1,
    mail_address: 'test@test.com',
    hashed_password: 123456,
    authority1: 1,
    authority4: 1,
    store_name: 'テスト店',
    company_name: 'テスト社',
    valid_flag: true,
  },
};

// console.log(initialState());

export const AuthReducer = reducerWithInitialState<AuthState>(initialState)
  .case(AuthActions.setUser, (state, payload) => ({
    ...state,
    user: payload ? cloneDeep(payload) : initialState.user,
  }))
  .case(AuthActions.resetState, () => initialState)
  .default((state) => state);
