import { useCallback, useEffect, useState } from 'react';
import { useDispatch } from 'react-redux';
import './test-page.pc.scss';
import { useDidMount } from '../../../../hooks/life-cycle';
import { Table } from '../../../ui/table/table';

export const TestPagePC = () => {
  const dispatch = useDispatch();
  const [select, setselect] = useState(NaN);

  const onClickRow = useCallback((row : any) => {
    setselect([
      { id: 1, text: 'aa' },
      { id: 2, text: 'cc' },
    ].findIndex((v2) => v2.id === row.id));
  }, []);

  return (
    <div>
      <Table
        className="table_selectable table_sortable table_sticky"
        header={['', '']}
        onClickRow={onClickRow}
        selectedTr={[select]}
        rowDataList={[
          { id: 1, text: 'aa' },
          { id: 2, text: 'cc' },
        ]}
        lists={[
          { id: 1, text: 'aa' },
          { id: 2, text: 'cc' },
        ].map((v:any, i) => ([
          v.id,
          v.text,
        ]))}
      />
    </div>
  );
};
